import React from "react";
import styled from "styled-components";

import { colors } from "../../utils/colors";

const Button = styled.button`
  cursor: pointer;
  border: none;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin: 0 16px 0 0;
  color: ${colors.white};

  background: ${(props) =>
    props.active ? colors.white : colors.transparentWhite};

  @media (min-width: 992px) {
    margin: 0 24px 0 0;
  }
`;

const ButtonDot = ({ active, onClick }) => (
  <Button type="button" active={active} onClick={() => onClick()} />
);

export default ButtonDot;
